.option {
  position: relative;
  letter-spacing: 0.15px;
  line-height: 150%;
}
.menuitem {
  background-color: var(--background-paper);
  width: 347px;
  height: 36px;
  justify-content: center;
  padding: var(--padding-7xs) var(--padding-base);
  box-sizing: border-box;
}
.list:hover .menuitem1,
.list:hover .menuitem2,
.list:hover .menuitem3,
.menuitem:hover {
  background-color: var(--color-gray-500);
}
.list,
.menuitem,
.menuitem1,
.menuitem2,
.menuitem3 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.menuitem1,
.menuitem2 {
  align-self: stretch;
  background-color: var(--background-paper);
  justify-content: center;
  padding: var(--padding-7xs) var(--padding-base);
}
.menuitem2:hover {
  background-color: #0ff000;
}
.list:active .menuitem1,
.menuitem2:active {
  background-color: var(--color-red);
}
.list,
.menuitem3 {
  box-sizing: border-box;
}
.menuitem3 {
  background-color: var(--background-paper);
  width: 347px;
  justify-content: center;
  padding: var(--padding-7xs) var(--padding-base);
}
.list {
  position: relative;
  width: 100%;
  justify-content: flex-start;
  padding: var(--padding-5xs) var(--padding-0);
  text-align: left;
  font-size: var(--typography-body-1-size);
  color: var(--text-primary);
  font-family: var(--typography-body-1);
}
